import React from 'react';
import Sidebar from '../../components/sidebar/sidebar';
import { makeStyles } from "@material-ui/core";
import styled from 'styled-components';


import MovieList from '../../components/movie-list/move-list';



const MovieListWrap = styled.div`
    margin-top: 100px;
    margin-left: ${props => !props.isOpen ? "100px" : "270px"}
`;


const Movies = ({open,handleDrawerClose}) => {

    return ( 
        <div>
            <Sidebar open={open} handleDrawerClose={handleDrawerClose}></Sidebar>
            <MovieListWrap isOpen={open}>
                <MovieList ></MovieList>

            </MovieListWrap>
        </div>
     );
}
 
export default Movies;